import './App.css';
import About from './About.js';
import Contact from './Contact.js'
import Experience from "./Experience";
import Header from './Header.js';
import Projects from './Projects.js';
import ProjectPopup from "./ProjectPopup";
import {useState} from "react";
function App(props) {

    const [currProjectInfo, setCurrProjectInfo] = useState([]);
    const isPopup = currProjectInfo.length !== 0;

    function handleClickProject(pName) {
        const projInfo = props.projectList.filter(p => {
            return p.name === pName
        })[0]
        setCurrProjectInfo(projInfo);
    }

    function handleCloseProject() {
        setCurrProjectInfo([]);
    }

    return <div className="App">
        <Header/>
        <About/>
        <Projects projectList={props.projectList}
                  handleClickProject={handleClickProject} />
        <Experience jobList={props.jobList} />
        <Contact/>
        {isPopup && <ProjectPopup info={currProjectInfo}
                                  handleCloseProject={handleCloseProject} />}
    </div>;
}

export default App;
