import './Project.css';

function Project(props) {

    const img_ratio_class = props.name.includes("App") ?
                                "main_app_img" : "main_other_img"

    return <div className="project_box" onClick={() => props.handleClickProject(props.name)}>
        <div className="box">
            <div className="icon"><i className="fas fa-search-plus"/></div>
            <div className="search_overlay"/>
            <h3>{props.name}</h3>
            <div className="main_img_section">
                {props.images.length > 0 &&
                    <img src={require("./" + props.images[0])} className={img_ratio_class} />}
            </div>
        </div>
    </div>;
}

export default Project;