import './Experience.css';

function Job(props) {
    return <li className="job">
        <img src={require("./" + props.image)} className="job_icon" />
        <div className="job_not_icon">
            <span className="job_title">{props.name}</span>,
            {" " + props.company}
            <br/> <span className="job_dates">{props.time}</span>
            <br/> <div className="job_descr">{props.description}</div>
        </div>
    </li>;
}

function Experience(props) {
    return <>
        <div className="anchor" id="experience" />
        <div className="experience">
            <h1 className="section_header">Experience:</h1>
            <ul className="experience_content">
                {props.jobList.map(j =>
                    <Job key={j.id} name={j.name}
                         time={j.time} company={j.company}
                         description={j.description} image={j.image} /> )}
            </ul>
        </div>
    </>;
}

export default Experience;